import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			// cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			pages: 0,
			params: {
				categories: '',
				sort: 'comprehensive',
				page: 0,
				pageSize: 10
			}
		};
	},
	
	onLoad(options){



		// this.cateId = options.tid;
		this.params.categories = options.tid;
		this.loadCateList(options.sid);
		this.loadData();
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom(){
		this.loadData();
	},
	methods: {
		//加载分类
		async loadCateList(sid){
			// let list = await this.$api.json('cateList');
			// let cateList = list.filter(item=>item.pid == fid);
			
			// cateList.forEach(item=>{
			// 	let tempList = list.filter(val=>val.pid == item.id);
			// 	item.child = tempList;
			// })
			// this.cateList = cateList;
			let params = {
				pid: sid
			}
			let { data } = await this.Api.getOneItemTypes(params);
			this.cateList = data;
			
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type='add', loading) {
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			let sort = this.sortName();
			let params = this.params;
			params.sort = sort;
			if(type === 'refresh'){
				params.page = 0;
			}
			params.page++;
			let { data: { docs,pages } } = await this.Api.getItemList(params);
			if(type === 'refresh'){
				this.goodsList = [];
			}
			this.pages = pages;
			this.goodsList = this.goodsList.concat(docs);
			
			//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
			this.loadingType  = this.params.page >= pages ? 'nomore' : 'more';
			if(type === 'refresh'){
				if(loading == 1){
					uni.hideLoading()
				}else{
					uni.stopPullDownRefresh();
				}
			}
		},
		sortName() {
			if(this.filterIndex == 0) {
				return 'comprehensive'
			}
			if(this.filterIndex == 1) {
				return 'sale'
			}
			if(this.filterIndex == 2 && this.priceOrder == 1) {
				return 'price_asc'
			}
			if(this.filterIndex == 2 && this.priceOrder == 2) {
				return 'price_des'
			}
		},
		//筛选点击
		tabClick(index){
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//显示分类面板
		toggleCateMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(()=>{
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item){
			console.log(item);
			this.params.categories = item._id;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(item){
			let id = item._id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		stopPrevent(){}
	},
}
